const faqItems: FAQItem[] = [
  {
    question: "What is Smart Queueing?",
    answer: "Smart Queueing is technology we built for A2P 10DLC text messaging that manages rate limits (throughputs) as they are sent by businesses to their consumers. Rate limit management is needed so that businesses are not exceeding their approved Carrier rate limits, which would result in failed messages, and conversely, so they are not being capped below their approved Carrier rate limits, preventing them from maximizing these limits."
  },
  {
    question: "How does Smart Queueing work?",
    answer: "This technology proactively (prior to sending a message) manages this for you at the Brand, Campaign, phone number, and Mobile Carrier level accordingly. Smart Queueing does a proactive automatic lookup to see who (AKA which Mobile Carrier) you are about to message and alerts you if you reach 50% of the limit, 75% of the limit, or 100% of the limit. At 100%, the technology simply queues the messages that would otherwise exceed your Carrier rate limit.",
  },
  {
    question: 'Why is it called "Smart Queueing"?',
    answer: 'There are different types of "queueing" in regards to A2P 10DLC text messaging. We wanted to differentiate general message queueing from our proactive, methodical approach to managing Carrier rate limits.',
  },
  {
    question: 'What are the benefits of having Smart Queueing?',
    answer: (<MultilineAnswer>
      <p>
        <Text.Regular.Normal.md>
          When Carrier rate limits are exceeded, the sender will experience a service disruption. The messages will fail (worst case scenario would be a Campaign or even Brand suspension), and you will still be charged for each attempted message plus Carrier pass-through fees. If messages are later re-sent, you have essentially been charged double.
        </Text.Regular.Normal.md>
      </p>
      <p>
        <Text.Regular.Normal.md>
          Smart Queueing prevents messages from ever exceeding (and therefore failing) Carrier rate limits. This eliminates the possibiliy of service disruptions, paying for failed messages, paying to re-send failed messages, and a flood of support tickets from frustrated customers in regards to exceeding these limits.
        </Text.Regular.Normal.md>
      </p>
      <p>
        <Text.Regular.Normal.md>
          Smart Queueing also eliminates the risk of being "capped" at the lowest Campaign rate limit within a Brand.
        </Text.Regular.Normal.md>
      </p>
    </MultilineAnswer>)
  },
  {
    question: "What happens to your messaging platform if you don't have a tool like Smart Queueing?",
    answer: (<div>
      <p>
        <Text.Regular.Normal.md>
          To our knowledge, there are two approaches that API providers are taking instead of offering comprehensive rate limit management solutions.
        </Text.Regular.Normal.md>
      </p>
      <StyledOl>
        <li>
          <Text.Regular.Bold.md>
            Lifting the Ceiling:&nbsp;
          </Text.Regular.Bold.md>
          <Text.Regular.Normal.md>
            API providers are able to "lift the ceiling" on Carrier rate limits from their platform. What this means is that they will allow software providers to send out any amount of messages, regardless of associated Campaign's approved Carrier rate limits. This lack of rate limit management leaves software providers vulnerable to exceeding limits and experiencing disruptions while still being charged for any failed messages.
          </Text.Regular.Normal.md>
        </li>
        <li>
          <Text.Regular.Bold.md>
            Capping at the Campaign Level:&nbsp;
          </Text.Regular.Bold.md>
          <Text.Regular.Normal.md>
            Alternatively, API providers can set a cap on the amount of messages software providers are able to send at the Campaign level so that you cannot exceed your approved Carrier rate limit. To do this, API providers would have to take the lowest approved throughputs across all Campaigns under each Brand, and make that the cap. Once the cap is reached, messages will be queued and in some cases, drastically delayed.
          </Text.Regular.Normal.md>
        </li>
      </StyledOl>
    </div>),
  },
  {
    question: 'Is Smart Queueing very technical feature to leverage or can anyone utilize it?',
    answer: (<MultilineAnswer>
      <p>
        <Text.Regular.Normal.md>
          This technology does not require any effort to implement, leverage, or oversee.
        </Text.Regular.Normal.md>
      </p>
      <p>
        <Text.Regular.Normal.md>
          Smart Queueing is automatically running across all 10DLC Brands and Campaign to manage Carrier rate limits for your customers. We have a direct integration with The Campaign Registry so that when you elect Telgorithm as your Connectivity Partner, Telgorithm receives all Carrier rate limit data so Smart Queueing can instantly begin managing on your behalf.
        </Text.Regular.Normal.md>
      </p>
    </MultilineAnswer>)
  },
  {
    question: "What is Telgorithm's error code for exceeding 10DLC Carrier limits?",
    answer: (<MultilineAnswer>
      <p>
        <Text.Regular.Normal.md>
          An error code implies that a messages has failed; Telgorithm has zero error codes for exceeding Carrier rate limits because Smart Queueing prevents this from happening.
        </Text.Regular.Normal.md>
      </p>
      <p>
        <Text.Regular.Normal.md>
          We send Queueing Alerts (via email) to notify our customers when a Brand or Campaign is sending at 50% and 75% of their approved Carrier limits. The purpose of these alerts is to proactively notify you that it may be time to apply for added vetting to get your limits increased. When you reach 100%, your messages are Smart Queued, meaning they're held until the limits are reset.
        </Text.Regular.Normal.md>
      </p>
    </MultilineAnswer>)
  },
  {
    question: 'How much does Smart Queueing cost?',
    answer: 'We do not charge for our Smart Queueing technology. Our belief is that it is fundamental to A2P 10DLC text messaging, so Smart Queueing is part of our core SMS API product.'
  }
]

